.comBasicTxt{
    margin-top: 12px;
    &:first-child {margin-top: 0;}
    &.mgH20 {margin-top: 20px;}
    &.mgH32 {margin-top: 32px;}
    .txt {
        margin-top:8px;font-size: rem(16px); line-height: 1.6; color: var(--Gray-800); word-break: keep-all;
        em {font-weight: 700;}
        &:first-child {margin-top: 0;}
    }
    .txtBold {
        margin-top:8px;font-size: rem(18px); font-weight: 700;line-height: 1.6; color: var(--Gray-800); word-break: keep-all;
        &:first-child {margin-top: 0;}
    }
    button.copy {
        width: 16px; height: 16px; margin-left: 2px; font-size: 0; background: url(../../images/common/ic_16_copy.svg) center / 16px no-repeat; display: inline-block;
    }

    // 컴포넌트 변경 Class
    // H3 하단 내부 리스트
    &.space {
        padding-left: 18px;
        @include media("mobile"){padding-left: 0;}
    }

    // 이미지 다음으로 오는 여백
    .comImgBox + & {margin-top: 32px;}

    @include media("mobile"){
        margin-top: 8px;
        &.mgH20 {margin-top: 16px;}
        &.mgH32 {margin-top: 20px;}
        .txt {
            margin-top: 4px; font-size: rem(15px);
        }
        .txtBold {
            margin-top:4px;font-size: rem(16px);
        }
        
        .comImgBox + & {margin-top: 24px;}
    }
}