
//기본 레이아웃
.lytLayout {
    .layoutArea{
        .layoutTop{
            width:100%; height:176px; background: #EDF5F2; /*position: sticky; left: 0; top: 0; z-index: 100;*/
            &:has(.cptHeaderEn) {
                height: 78px;
            }
            @include media('mobile'){
                height:70px;
            }
        }
        .layoutContainer{
            width:100%;
            .layoutCont{

            }
            .layoutSubCont{
                width: 100%;
                .layoutContArea {
                    width: 100%; max-width: 1480px; padding: 60px 20px 0; margin: 0 auto 120px; position: relative;
                    @include media('mobile'){
                        padding: 40px 16px 0;
                    }
                }
            }
        }
        .layoutFooter{

        }
        // 로그인 페이지 배경 컬러 변경
        &.bgColor {background: var(--Bg-Line-bg-100, #F9FAFC);}
    }
}

